.about {
  background-color: $white;

  &--wrapper {
    padding: 32px 24px 52px 24px;
    text-align: center;
  }
}

.about__title {
  font-size: $font-size-m;
  color: $black;
  max-width: 500px;
  margin: 0 auto 35px;

  @media all and (min-width: $tablet) {
    font-size: $font-size-l;
  }
}

.about__description {
  font-weight: $font-weight-normal;
  color: $light-black;
}
